<template>
  <div class="layout">
    <div class="header">
      <div class="username">{{ companyInfo.name }}</div>
      <div class="useraccount">{{ companyInfo.number }}</div>
      <div class="operateName">{{ userInfo.name }}</div>
      <div class="exist" @click="exit"></div>
      <!-- <div class="line-one">
        <div class="toFirst" @click="$router.push('/')"></div>
        <div class="back" @click="$router.push('/')"></div>
      </div> -->

    </div>
    <div>
      <div class="line-two">
        <div class="line-two-content" style="background: #1f93d5;">
          <el-radio-group v-model="tabPosition" size="big" @change="listChange">
            <el-radio-button label="billQuery">账单查询</el-radio-button>
            <el-radio-button label="moneyTransfer">转账业务</el-radio-button>
            <el-radio-button label="cashManagement">现金管理</el-radio-button>
            <el-radio-button label="paymentBusiness">缴费业务</el-radio-button>
            <el-radio-button label="bottom">票据业务</el-radio-button>
            <el-radio-button label="top3">国际业务</el-radio-button>
            <el-radio-button label="left">信贷融资</el-radio-button>
            <el-radio-button label="top4">投资理财</el-radio-button>
            <el-radio-button label="left1">隐藏</el-radio-button>
          </el-radio-group>
          <el-radio-group v-model="tabPosition" style="margin-top: -1px;" size="big" @change="listChange">
            <el-radio-button label="top111">电子商务</el-radio-button>
            <el-radio-button label="top22">财政社保</el-radio-button>
            <el-radio-button label="right33">卡类业务</el-radio-button>
            <el-radio-button label="serviceManage">服务管理</el-radio-button>
            <el-radio-button label="bottom55">特色业务</el-radio-button>
          </el-radio-group>

          <!-- <router-link :to="'/main/account'" class="router-link-class">账户</router-link>
          <router-link :to="'/main/pay'" class="router-link-class">付款</router-link>
          <span class="router-link-class">存款</span>
          <span class="router-link-class">投资</span>
          <span class="router-link-class">现金管理</span>
          <span class="router-link-class">收款</span>
          <router-link :to="'/main/check'" class="router-link-class">复核发送</router-link> -->
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'Layout',
  computed: {
    companyInfo () {
      return this.$store.state.companyInfo
    },
    userInfo () {
      return this.$store.state.userInfo
    },
    routeInfo () {
      return this.$store.state.routeInfo
    },
    role () {
      return this.$store.state.role
    }
  },
  watch: {
    routeInfo (val) {
      this.tabPosition = val
    }
  },
  data () {
    return {
      tabPosition: 'billQuery'
    }
  },
  created () {
    this.listChange()
  },
  methods: {
    listChange () {
      if (this.tabPosition === 'billQuery') {
        this.$store.commit('saveRouteInfo', 'billQuery')
        this.$router.push('/main/account')
      } else if (this.tabPosition === 'moneyTransfer') {
        this.$store.commit('saveRouteInfo', 'moneyTransfer')
        // 根据身份进行不同的路由跳转
        switch (parseInt(this.role)) {
          case 1: // 出纳
            this.$router.push('/main/pay')
            break
          case 2: // 主管
            this.$router.push('/main/pay/pay-check')
            break
        }
      } else if (this.tabPosition === 'serviceManage') {
        this.$store.commit('saveRouteInfo', 'serviceManage')
        this.$router.push('/main/serviceManage')
      }
    },
    exit () {
      // 安全退出
      this.$store.commit('changeLoginState', 0)
      this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped>
  .layout {
    width: 1150px;
    margin: 0 auto;
    height: auto;
    .router-link-active {
      position: relative;
      color: #333333;
    }
    .router-link-active:after {
      position: absolute;
      bottom: -10px;
      width: 100%;
      left: 0;
      height: 4px;
      content: '';
      background-color: #27bfb1;
    }
    .header {
      background: url("../../assets/bank-main-01.png") no-repeat center;
      height: 70px;
      position: relative;
      .username{
        position: absolute;
        top: 42px;
        width: 158px;
        left: 762px;
        font-size: 12px;
      }
      .useraccount{
        position: absolute;
        top: 45px;
        width: 111px;
        left: 954px;
        font-size: 12px;
      }
      .operateName{
        position: absolute;
        top: 42px;
        width: 50px;
        font-size: 12px;
        left: 1075px;
      }
      .exist{
        position: absolute;
        top: 10px;
        width: 55px;
        font-size: 12px;
        left: 1084px;
        height: 30px;
        cursor: pointer;
      }
      .line-one {
        width: 100%;
        height: 35px;
        background-color: transparent;
        .toFirst {
          position: absolute;
          left: 0;
          top: 5px;
          width: 90px;
          height: 30px;
          cursor: pointer;
        }
        .back {
          position: absolute;
          right: 98px;
          top: 5px;
          width: 90px;
          height: 30px;
          cursor: pointer;
        }
      }
      .line-two {
        width: 100%;
        height: 65px;
        background-color: transparent;
        position: relative;

        .line-two-content {
          position: absolute;
          left: 300px;
          top: 30px;
          width: 715px;
          .el-radio-group.el-radio-button__orig-radio:checked .el-radio-button__inner{
            background-color: red;
          }

        }
      }
    }
  }
</style>

